// src/components/servicios/ReportModal.tsx
import React from 'react'
import { VentaServicio } from '../../../entities/servicios'

interface Props {
  selectedReport: VentaServicio
  setSelectedReport: (r: null) => void
  formatCurrency: (val: number) => string
  formatDate: (val: string) => string
  exportToCSV: (venta: VentaServicio) => void
}

export default function ReportModal({
  selectedReport,
  setSelectedReport,
  formatCurrency,
  formatDate,
  exportToCSV
}: Props) {
  return (
    <div className="modal-overlay">
      <div className="modal-box">
        <div className="modal-header">
          <h2>Reporte Venta Servicio #{selectedReport.idVentaServicio}</h2>
          <button className="close-btn" onClick={() => setSelectedReport(null)}>
            ✖
          </button>
        </div>

        <div className="modal-content">
          <div className="summary-card">
            <h4>ID Cliente</h4>
            <p>{selectedReport.idCliente}</p>
          </div>

          <div className="summary-card">
            <h4>Costo Total</h4>
            <p>{formatCurrency(selectedReport.costoTotal)}</p>
          </div>

          <div className="summary-card">
            <h4>Fecha</h4>
            <p>{formatDate(selectedReport.fechaRegistro)}</p>
          </div>

          <div className="modal-actions">
            <button className="primary-btn" onClick={() => exportToCSV(selectedReport)}>
              Exportar CSV
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
