import React from 'react'
import ReportFilters from '../../components/servicios/ReportFilters'
import ReportsTable from '../../components/servicios/ReportsTable'
import ReportModal from '../../components/servicios/ReportModal'
import ServiceForm from '../../components/servicios/ServiceForm'
import ServicesTable from '../../components/servicios/ServicesTable'
import useGestionServicios from '../../../features/useGestionServicios'
import '../../../styles/productos.css'

export default function GestionServiciosPage() {
  const {
    activeTab,
    setActiveTab,
    filters,
    setFilters,
    searchTerm,
    setSearchTerm,
    reports,
    reportServices,
    selectedReport,
    setSelectedReport,
    technicianOptions,
    generateReport,
    clearFilters,
    isGeneratingReport,
    exportToCSV,
    viewReportDetails,
    formatCurrency,
    formatDate
  } = useGestionServicios()

  return (
    <div className="dashboard-container">
      <h1 className="text-2xl font-bold mb-4">Gestión de Servicios</h1>

      <div className="tabs-container">
        <button className={`tab-btn ${activeTab === 'registrar' ? 'active' : ''}`} onClick={() => setActiveTab('registrar')}>
          Registrar Servicio
        </button>
        <button className={`tab-btn ${activeTab === 'reportes' ? 'active' : ''}`} onClick={() => setActiveTab('reportes')}>
          Reportes
        </button>
      </div>

      {activeTab === 'registrar' && (
        <>
          <ServiceForm />
          <div className="box-white">
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="search-input"
              placeholder="Buscar servicios..."
            />
            <ServicesTable searchTerm={searchTerm} />
          </div>
        </>
      )}

      {activeTab === 'reportes' && (
        <>
          <div className="box-white">
            <h2 className="section-title">Filtros para Reporte</h2>
            <ReportFilters 
              filters={filters} 
              setFilters={setFilters} 
              technicianOptions={technicianOptions} 
            />
            <div className="action-buttons">
              <button
                onClick={generateReport}
                className="primary-btn"
                disabled={isGeneratingReport}
              >
                <span className="icon">📄</span>
                {isGeneratingReport ? 'Generando...' : 'Generar Reporte'}
              </button>
              <button
                onClick={clearFilters}
                className="filter-btn"
              >
                <span className="icon">🧹</span>
                Limpiar
              </button>
            </div>
          </div>

          <div className="box-white">
            <h2 className="section-title">Reportes Existentes</h2>
            <ReportsTable 
              reports={reports} 
              technicianOptions={technicianOptions} 
              formatCurrency={formatCurrency} 
              formatDate={formatDate} 
              exportToCSV={exportToCSV} 
              viewReportDetails={viewReportDetails}
            />
          </div>

          {selectedReport && (
            <ReportModal
              selectedReport={selectedReport}
              setSelectedReport={setSelectedReport}
              technicianOptions={technicianOptions}
              reportServices={reportServices}
              formatCurrency={formatCurrency}
              formatDate={formatDate}
              exportToCSV={exportToCSV}
            />
          )}
        </>
      )}
    </div>
  )
}
