import React from 'react'

interface Report {
  id: number
  name: string
  total: number
  technician: string
  date: string
  services: number
}

interface Technician {
  id: string
  name: string
}

interface Props {
  reports: Report[]
  technicianOptions: Technician[]
  formatCurrency: (val: number) => string
  formatDate: (date: string) => string
  exportToCSV: (report: Report) => void
  viewReportDetails: (report: Report) => void
}

export default function ReportsTable({
  reports,
  technicianOptions,
  formatCurrency,
  formatDate,
  exportToCSV,
  viewReportDetails
}: Props) {
  return (
    <div className="products-table-container">
      <table className="products-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Nombre</th>
            <th>Total</th>
            <th>Técnico</th>
            <th>Fecha</th>
            <th>Servicios</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {reports.length > 0 ? (
            reports.map((report) => (
              <tr key={report.id}>
                <td>{report.id}</td>
                <td>{report.name}</td>
                <td>{formatCurrency(report.total)}</td>
                <td>{technicianOptions.find(t => t.id === report.technician)?.name || 'Todos'}</td>
                <td>{formatDate(report.date)}</td>
                <td>{report.services}</td>
                <td>
                  <div className="actions-container">
                    <button className="action-btn edit" title="Ver" onClick={() => viewReportDetails(report)}>Ver</button>
                    <button className="action-btn export" title="Exportar" onClick={() => exportToCSV(report)}>Exportar</button>
                  </div>
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan={7} className="text-center">No hay reportes generados aún</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  )
}
